#loginForm{
    height: 260px;
    width: 300px;
    border: 3px solid burlywood;
    margin-left: auto;
    margin-right: auto;
    background-color: navajowhite;
    margin-top: 200px;
    text-align: center;
    border-radius: 20px;
    padding-top: 20px;
    padding-bottom: 60px;
}

#login{
    font-size: 30px;
    color: steelblue;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.inputFiled{
    margin: 10px 0px;
    height: 25px;
    width: 200px;
    border-radius: 20px;
    padding-left: 10px;
}

#checkbox{
    margin-top: 15px;
    font-size: 60px;
    width: 15px;
    height: 15px;
}

#btn{
    height: 40px;
    width: 80px;
    border-radius: 15px;
    background-color: steelblue;
    margin-top: 10px;
}